.searchbar {
   top: 0;
   left: 0;
   position: sticky;
   z-index: 1100;
   display: flex;
   justify-content: center;
   align-items: center;
   min-height: 64px;
   padding-right: 24px;
   padding-left: 24px;
   padding-top: 12px;
   padding-bottom: 12px;
   color: #fff;
}

.form {
   display: flex;
   align-items: center;
   width: 100%;
   max-width: 600px;
   background-color: #fff;
   border-radius: 5px;
   overflow: hidden;

   box-shadow:
      0px 2px 4px -1px rgba(0, 0, 0, 0.2),
      0px 4px 5px 0px rgba(0, 0, 0, 0.14),
      0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

.button {
   display: inline-block;
   width: 50px;
   height: 50px;
   border: 0;

   background-size: 40%;
   background-repeat: no-repeat;
   background-position: center;
   background-color: rgb(204, 204, 204);

   opacity: 0.6;
   transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
   cursor: pointer;
   outline: none;
}

.button:hover {
   opacity: 2;
}

.button__label {
   position: absolute;
   width: 1px;
   height: 1px;
   padding: 0;
   overflow: hidden;
   clip: rect(0, 0, 0, 0);
   white-space: nowrap;
   clip-path: inset(50%);
   border: 0;
}

.input {
   display: inline-block;
   width: 100%;
   font: inherit;
   font-size: 20px;
   border: none;
   outline: none;
   padding-left: 8px;
   padding-right: 8px;
   line-height: 1.5;
}

.input::placeholder {
   font: inherit;
   font-size: 18px;
}
